import ja from "./ja";

// å˜ç´”ãª t é–¢æ•°ï¼ˆä»Šå›ã¯æ—¥æœ¬èªå›ºå®šï¼‰
export const t = (path, ...args) => {
  const segs = path.split(".");
  let cur = ja;
  for (const s of segs) {
    if (cur == null) return path;
    cur = cur[s];
  }
  if (typeof cur === "function") return cur(...args);
  return cur ?? path;
};

// --- compatibility export (named + default) ---
export default t;
